        margin: 0 auto 5px auto;
    }
}

@media (max-width:1000px) {
    .Footer {
        font-size: 11px;
    }
}

@media (max-width:700px) {
    .Footer {
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-align: center;
            align-items: center;
        text-align: center;
    }
}



